<template>
    <div id="editone">
        <h3>编辑员工页面</h3>
        <div class="row">

            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" disabled v-model="employee_id" required />
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required />
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="dept" required />
                    </div>
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required />
                    </div>
                </div>

                <button class="btn red" type="submit">Submit</button>
                <router-link to="/" class="btn gray">Cancel</router-link>
            </form>

        </div>
    </div>
</template>

<script>

import db from './firebaseInit.js'

export default {
    name:'editone',
    data(){
        return {
            employee_id:null,
            name:null,
            dept:null,
            position:null
        }
    },
    beforeRouteEnter(to,from,next){
        db.collection('employees').where('employee_id','==',to.params.employee_id)
        .get()
        .then(querySnapshot=>{
            querySnapshot.forEach(doc=>{
                next(vm=>{
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.dept = doc.data().dept
                    vm.position = doc.data().position

                })
            })
        })
    },
    watch:{
        '$route':'fetchData'
    },
    methods:{
        fetchData(){
            db.collection('employees').where('employee_id','==',this.$route.params.employee_id).get().then(querySnapshot=>{
                querySnapshot.forEach(doc=>{
                    this.employee_id = doc.data().employee_id,
                    this.name = doc.data().name
                    this.dept = doc.data().dept
                    this.position = doc.data().position
                })
            })
        },
        updateEmployee(){
            db.collection('employees').where('employee_id','==',this.$route.params.employee_id).get().then(querySnapshot=>{
                querySnapshot.forEach(doc=>{
                    doc.ref.update({
                        employee_id:this.employee_id,
                        name:this.name,
                        dept:this.dept,
                        position:this.position
                    })
                    .then(()=>{
                        this.$router.push({name:'checkall',params:{employee_id:this.employee_id}})
                    })
                })
            })
        }
    }
}
</script>



