import Vue from 'vue'
import Router from 'vue-router'
import first from '@/components/first'
import editone from '@/components/newone'
import checkall from '@/components/checkall'
import addcount from '@/components/opennew'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'first',
      component: first
    },
    {
      path: '/new',
      name: 'addcount',
      component: addcount
    },
    {
      path: '/edit/:employee_id',
      name: 'editone',
      component: editone
    },
    {
      path: '/:employee_id',
      name: 'checkall',
      component: checkall
    },
  ]
})



